  <!--商品信息项-->
  <template>
  <div class="cms-form-layout loading-top product-main-warpper" v-loading='loading' :element-loading-text="loadingTitle">
    <el-form ref="form" :rules="rules" :model="formData" label-width="210px" @submit.native.prevent>
      <el-form-item label="售价" prop="coreModel.salePrice" ref="coreModel_salePrice">
        <el-input v-model="formData.coreModel.salePrice"></el-input>
        <span class="gray">价格控制在0.01~99999999之间的数字，且不能高于市场价，如商品设 置SKU，此价格自动记录为最低SKU价格
        </span>
      </el-form-item>
      <el-form-item label="市场价" prop="coreModel.marketPrice" ref="product_name">
        <el-input v-model="formData.coreModel.marketPrice">
        </el-input>
      </el-form-item>
      <el-form-item label="成本价" prop="coreModel.costPrice">
        <el-input v-model="formData.coreModel.costPrice">
        </el-input>
      </el-form-item>
      <el-form-item label="库存量" prop="coreModel.inventory" ref="coreModel_inventory">
        <el-input v-model="formData.coreModel.inventory"></el-input>
        <span class="gray">库存量控制在1~99999999之间的整数，如商品设置SKU，库存量自动计 算为所有SKU库存总和
        </span>
      </el-form-item>
      <el-form-item label="库存预警值" prop="coreModel.warningValue">
        <el-input v-model="formData.coreModel.warningValue"></el-input>
        <span class="gray">库存预警值必须少于库存量，如商品设置SKU，预警值自动计算为所有 SKU设定的预警值总和
        </span>
      </el-form-item>
      <el-form-item label="商品规格" prop="coreModel.hasSku">
        <div>
          <el-checkbox v-model="formData.coreModel.hasSku">启用商品规格</el-checkbox>
          <span class="gray">启用商品规格、商品的价格、库存、以SKU信息为准</span>
        </div>
        <!-- 规格信息获取 -->
        <el-checkbox-group v-model="checkSaleStandardIds" @change="setProperties" v-if="formData.coreModel.hasSku">
          <section class="standard-group" v-for="(group,index) in saleStandardInfos" :key="group.id">
            <div class="standard-group-header">
              <div class="fl">
                <span class="standard-group-name">{{group.name}}</span>
                <span class="standard-group-desc" v-if="group.valueType==4">( 该规格需要上传图片 )</span>
                <!-- 自定义规格可以删除 -->
                <span class="el-icon-delete del" v-if="group.attrType===2" @click="deleteCustomStandard(group,index)"></span>
              </div>
              <div class="fr">
                <label class="standard-group-desc">规格值名称</label>
                <el-input style="width:110px" v-model="customStandardValue['value'+index]"></el-input>
                <cms-button size='small' @click='addCustomStandard(index)' style="margin-left:10px;">添加</cms-button>
              </div>
            </div>
            <div class="standard-group-body">
              <!-- 判断下是图片还是复选类型 -->
              <ul v-if="group.valueType===4" class="clearfix">
                <li v-for="(item,index2) in group.attrValueList" :key="item.id" class="type-img-items">
                  <cms-picture-zone :preview='false' :index='index2' :params="{parentIndex:index}" class="product-img-box" @change="getStandardImg">
                    <div class="product-img-warpper">
                      <img v-if="item.valueResourceId&&item.valueResourceId!==''" :src="$cms.filterUrl(item.url)" alt="">
                      <span v-else class="img-plus el-icon-plus"></span>
                    </div>
                  </cms-picture-zone>
                  <div class="img-txt">
                    <el-checkbox :label="item.id" class="check-box">
                      <span class="type-value">{{item.attrValue}}</span>
                      <div class="el-icon-delete del" @click="deleteCustomStandardValue(item,index,index2)" v-if="item.attrType==2"></div>
                    </el-checkbox>
                  </div>
                </li>
              </ul>
              <ul v-else class="clearfix">
                <li v-for="(item,index2) in group.attrValueList" :key="item.id" class="type-checkbox-items">
                  <el-checkbox :label="item.id">
                    <span class="type-value">{{item.attrValue}}</span>
                    <!-- 自定义规格值可以删除 -->
                    <div class="el-icon-delete del" @click="deleteCustomStandardValue(item,index,index2)" v-if="item.attrType==2"></div>
                  </el-checkbox>
                </li>
              </ul>
            </div>
          </section>
          <div style="margin:20px 0 0 0">
            <cms-button icon='add' size='small' @click="popoverVisible=true">添加规格</cms-button>
          </div>
          <!-- 添加规格弹窗 -->
          <el-dialog title="添加规格" top="35vh" :visible='popoverVisible' width="640px">
            <div class="dialog-form">
              <el-form :model="popoverForm" ref='dialogForm'>
                <el-form-item label="规格名称" prop="name" label-width="120px" :rules="
                [{ required: true, message: '必须输入规格名称', trigger: 'blur' }]">
                  <el-input v-model="popoverForm.name"></el-input>
                </el-form-item>
                <el-form-item label="规格值" label-width="120px" prop="attrValues" :rules="
                [{ required: true, message: '必须输入规格值', trigger: 'blur' }]">
                  <el-input v-model="popoverForm.attrValues" type='textarea' :rows="4" placeholder="多个属性按回车输入"></el-input>
                </el-form-item>
                <div class="center">
                  <cms-button @click="addCustomStandardItem" style="margin-right:10px;">确认</cms-button>
                  <cms-button @click="clearCustomStandardItem" type='info'>取消</cms-button>
                </div>
              </el-form>
            </div>
          </el-dialog>
        </el-checkbox-group>
      </el-form-item>
      <!-- sku信息 -->
      <el-form-item label="sku信息" prop="name" v-if='formData.coreModel.hasSku&&skus.length>0'>
        <table class="sku-table">
          <tr>
            <th v-for="(item,index) in properties" :key="index" v-if="item.attrValueList.length>0" rowspan='2'>
              {{item.name}}
            </th>
            <th>售价</th>
            <th>市场价</th>
            <th>成本价</th>
            <th>库存</th>
            <th>预警值</th>
            <th rowspan='2' width='60'>操作</th>
          </tr>
          <tr>
            <td>
              <el-input class="table-input" type="number" :min="0" v-model="batch.salesPrice">
                <template slot="append">
                  <i class="iconfont icon-zhankai1-copy" @click="batchPrice('salesPrice')"></i>
                </template>
              </el-input>
            </td>
            <td>
              <el-input class="table-input" type="number" :min="0" v-model="batch.marketPrice">
                <template slot="append">
                  <i class="iconfont icon-zhankai1-copy" @click="batchPrice('marketPrice')"></i>
                </template>
              </el-input>
            </td>
            <td>
              <el-input class="table-input" type="number" :min="0" v-model="batch.costPrice">
                <template slot="append">
                  <i class="iconfont icon-zhankai1-copy" @click="batchPrice('costPrice')"></i>
                </template>
              </el-input>
            </td>
            <td>
              <el-input class="table-input" type="number" :min="0" v-model="batch.inventory">
                <template slot="append">
                  <i class="iconfont icon-zhankai1-copy" @click="batchPrice('inventory')"></i>
                </template>
              </el-input>
            </td>
            <td>
              <el-input class="table-input" type="number" :min="0" v-model="batch.warning">
                <template slot="append">
                  <i class="iconfont icon-zhankai1-copy" @click="batchPrice('warning')"></i>
                </template>
              </el-input>
            </td>
          </tr>
          <tr v-for="(item,index2) in skus" :key="index2">
            <td v-for="(name,index3) in item.name" :key="index3" class="min-w-90">
              {{name}}
            </td>
            <td>
              <el-input v-model="item.salesPrice" type="number" :min="0"></el-input>
            </td>
            <td>
              <el-input v-model="item.marketPrice" type="number" :min="0"></el-input>
            </td>
            <td>
              <el-input v-model="item.costPrice" type="number" :min="0"></el-input>
            </td>
            <td>
              <el-input v-model="item.inventory" type="number" :min="0"></el-input>
            </td>
            <td>
              <el-input v-model="item.warning" type="number" :min="0"></el-input>
            </td>
            <td>
              <i class="el-icon-delete del"></i>
            </td>
          </tr>
        </table>
        <span class="gray">注：商品实际销售SKU不存在的组合，可删除，系统将默认保存为</span>
      </el-form-item>
      <!-- 商品属性信息 -->
      <el-form-item label="商品属性" prop="name" v-if="productStandardInfos.length>0">
        <section class="standard-group" v-for="(group,index) in productStandardInfos" :key="group.id" :style="index===0?{marginTop:0}:''">
          <div class="standard-group-header">
            <div class="fl">
              <span class="standard-group-name">{{group.attrGroupName}}</span>
            </div>
            <div class="fr">
              <label class="standard-group-desc">规格值名称</label>
              <el-input style="width:110px" v-model="customAttributeValue['value'+index]"></el-input>
              <cms-button size='small' style="margin-left:10px;" @click="addCustomAttribute(index)">添加</cms-button>
            </div>
          </div>
          <div class="standard-group-body" style="padding:20px;">
            <!-- 属性组里面的属性项遍历 -->
            <div class="clearfix" :class="[standard.valueType==1?'w-50':'w-100']" v-for='(standard,index2) in group.attrs' :key="standard.id">
              <label class="fl  attribute-name">{{standard.name}}</label>
              <!-- 多选 -->
              <ul class="fl" v-if="standard.valueType===3 && standard.attrValueList.length > 0">
                <li v-for="(item) in standard.attrValueList" :key="item.id" class="type-checkbox-items">
                  <el-checkbox :label="item.id" @change="setProductInfos(item,standard,1)">
                    <span class="type-value">{{item.attrValue}}</span>
                  </el-checkbox>
                </li>
              </ul>
              <!-- 单选 -->
              <ul class="fl" v-if="standard.valueType===2 && standard.attrValueList.length > 0">
                <!-- 加一个字符串让他变成对象0.0 -->
                <el-radio-group v-model="attributeGroups[standard.id+'s']">
                  <li v-for="(item) in standard.attrValueList" :key="item.id" class="type-radio-items">
                    <el-radio :label="item.id" @change="setProductInfos(item,standard,2)">
                      <span class="type-value">{{item.attrValue}}</span>
                    </el-radio>
                  </li>
                </el-radio-group>
              </ul>
              <!-- 文本 -->
              <div v-if="standard.valueType===1">
                <el-input @blur='setProductInfos($event,standard,3)' style="width:220px;position: relative;top:0px;"></el-input>
                <div class="el-icon-delete del" @click="deleteCustomAttribute(standard,index,index2)" v-if="standard.attrType==2"></div>
              </div>

            </div>
          </div>
        </section>
      </el-form-item>
    </el-form>
    <div class="product-footer" :class="[fixed?'fixed-footer':'']">
      <cms-button @click="prevStep()" style="background:#f7f7f7;color:#444">上一步</cms-button>
      <cms-button v-color>下一步,设置关联</cms-button>
      <cms-button v-color @click="save()" style="background:#efae00">立即发布</cms-button>
    </div>
  </div>
</template>
  <script>
import rules from '@/rules'
import {
  productStandardValue,
  productStandardDelete
} from '@/api/product/type.js'

import {
  productSave,
  customStandard,
  customAttribute,
  customStandardDelete,
  customStandardItem
} from '@/api/product/product.js'

import { CreateSkus } from '@/utils/descartes'
var defaultData = {
  coreModel: {
    salePrice: '',
    marketPrice: 0.0,
    costPrice: 0.0,
    inventory: 10000,
    warningValue: 10,
    hasSku: false
  },
  skuTypeAttrs: [],
  skus: [],
  proTypeAttrs: []
}
export default {
  data() {
    return {
      loading: false,
      status: false,
      fixed: true,
      popoverVisible: false,
      popoverForm: {
        name: '',
        attrValues: ''
      },
      loadingTitle: '正在保存,请勿进行其他操作',
      formData: Object.assign({}, defaultData),
      batch: {
        salesPrice: '',
        costPrice: '',
        inventory: '',
        marketPrice: '',
        warning: ''
      },
      rules: {
        'coreModel.salePrice': [
          rules.required('必须输入一个价格'),
          rules.float()
        ],
        'coreModel.inventory': [
          rules.required('必须设置一个库存'),
          rules.number()
        ],
        'coreModel.hasSku': [rules.required()]
      },
      customStandardValue: {}, // 自定义规格值
      customAttributeValue: {}, // 自定义属性值
      checkSaleStandardIds: [], // 选中的规格id
      checkProductStandardIds: [], // 选中的属性id
      properties: [], // 选中的商品规格
      attributes: [], // 选中的属性值
      saleStandardInfos: [], // 销售属性集合
      productStandardInfos: [], // 商品属性集合
      skus: [], // 生成的sku信息
      attributeGroups: {},
      productTypeId: 0, // 商品类型id
      storeId: 0 // 商家id
    }
  },
  computed: {
    allCheckedLength: function() {
      // 判断勾选改变sku信息
      var len = 0
      this.properties.forEach(v => {
        len = len + v.attrValueList.length
      })
      return len
    }
  },
  methods: {
    getProductInfo(id) {
      this.productTypeId = this.$store.state.product.productTypeId // 从前一个页面获取到店家id 和商品类型id 设置一下
      this.storeId = this.$store.state.product.storeId
      productStandardValue({
        productTypeId: this.productTypeId,
        storeId: this.storeId
      }).then(res => {
        // 获取商品对应的属性分组值
        if (res.data && res.data.saleAttrs.length > 0) {
          this.saleStandardInfos = res.data.saleAttrs
          const arr = []
          this.saleStandardInfos.map(v => {
          // 初始化分组信息
            arr.push({
              valueType: v.valueType,
              attrGroupName: v.attrGroupName,
              name: v.name,
              id: v.id,
              attrValueList: []
            })
          })
          this.properties = arr
          this.productStandardInfos = res.data.nsaleAttrs
        }
        this.loading = false
      })
    },
    setProperties(item) {
      // 获得选中的规格
      const ids = this.checkSaleStandardIds

      this.saleStandardInfos.forEach((v, i) => {
        const tmp = []
        v.attrValueList.forEach((vv, j) => {
          if (ids.indexOf(vv.id) > -1) {
            tmp.push(vv)
          }
        })
        this.properties[i].attrValueList = tmp
      })
    },
    setProductInfos(item, parentItem, type) {
      // 获得选中的属性 type=1多选 2单选 3 文本
      let flag = true // 判断是否是同一个属性的多个值
      const obj = {
        groupName: parentItem.attrGroupName,
        attrName: parentItem.name,
        attrVc: item.attrCode,
        proTypeAttrId: parentItem.id
      }
      if (type === 3) {
        obj.attrVc = item.target.value
      } else {
        obj.attrVc = item.attrCode
      }

      if (this.attributes.length === 0) {
        this.attributes.push(obj)
      } else {
        for (const v of this.attributes) {
          if (v.proTypeAttrId === obj.proTypeAttrId) {
            const codeArr = v.attrVc.split(',')
            if (type === 1) {
              // 如果是复选,处理掉相同项
              const indexOf = codeArr.indexOf(obj.attrVc)
              if (indexOf > -1) {
                // 存在就删除
                codeArr.splice(indexOf, 1)
              } else {
                // 不存在就添加
                codeArr.push(obj.attrVc)
              }
              v.attrVc = codeArr.join(',')
            } else {
              // 单选和文本都做覆盖
              v.attrVc = obj.attrVc
            }
            flag = false
            break
          }
        }
        if (flag) {
          this.attributes.push(obj)
        }
      }
    },
    createdSkus() {
      // 构造sku属性信息
      const data = []
      this.properties.forEach(v => {
        // 把选中的属性过滤好
        data.push(v.attrValueList)
      })

      this.skus = CreateSkus(data)
    },
    batchPrice(name) {
      // 快速填写商品价格
      this.skus.forEach(v => {
        v[name] = this.batch[name]
      })
    },
    addCustomStandardItem(index) {
      // 商家新增规格
      this.$refs['dialogForm'].validate(valid => {
        if (valid) {
          customStandardItem({
            attrGroupName: '自定义添加',
            name: this.popoverForm.name,
            attrValues: this.popoverForm.attrValues,
            typeId: this.productTypeId,
            storeId: this.storeId,
            valueType: 2
          }).then(res => {
            this.saleStandardInfos.push(res.data)
            // 新增一个规格要同步一下规格属性
            this.properties.push({
              valueType: res.data.valueType,
              attrGroupName: res.data.attrGroupName,
              name: res.data.name,
              id: res.data.id,
              attrValueList: []
            })

            this.popoverVisible = false
            this.popoverForm.name = ''
            this.popoverForm.attrValues = ''
          })
        } else {
          return false
        }
      })
    },
    clearCustomStandardItem() {
      this.popoverVisible = false
      this.popoverForm.name = ''
      this.popoverForm.attrValues = ''
    },
    addCustomStandard(index) {
      // 商家新增自定义规格属性值
      const params = {
        attrValue: this.customStandardValue['value' + index],
        storeId: this.storeId,
        attrId: this.saleStandardInfos[index].id
      }
      customStandard(params).then(res => {
        if (res.code === 200) {
          const obj = {
            attrCode: res.data.attrCode,
            attrValue: res.data.attrValue,
            id: res.data.id,
            attrType: res.data.attrType,
            valueResourceId: ''
          }
          this.saleStandardInfos[index].attrValueList.push(obj)
        }
      })
    },
    addCustomAttribute(index) {
      // 商家自定义属性值
      const params = {
        name: this.customAttributeValue['value' + index],
        attrGroupName: this.productStandardInfos[index].attrGroupName,
        attrValues: 'no',
        typeId: this.productTypeId,
        storeId: this.storeId
      }
      customAttribute(params).then(res => {
        const obj = {
          attrGroupName: res.data.attrGroupName,
          attrValueList: res.data.attrValueList,
          custom: res.data.custom,
          id: res.data.id,
          name: res.data.name,
          valueType: res.data.valueType,
          attrType: res.data.attrType
        }
        this.productStandardInfos[index].attrs.push(obj)
      })
    },
    deleteCustomStandard(item, index) {
      productStandardDelete({ ids: [item.id] }).then(res => {
        if (res.code === 200) {
          this.saleStandardInfos.splice(index, 1)
        }
      })
      // 删除自定义规格
    },
    deleteCustomStandardValue(item, parentIndex, index) {
      // 删除自定义规格值
      customStandardDelete({ ids: [item.id] }).then(res => {
        if (res.code === 200) {
          const $index = this.checkSaleStandardIds.findIndex(
            v => v.id === item.id
          )
          this.checkSaleStandardIds.splice($index, 1)
          this.setProperties(this.checkSaleStandardIds)
          this.saleStandardInfos[parentIndex].attrValueList.splice(index, 1)
        }
      })
    },
    deleteCustomAttribute(item, parentIndex, index) {
      // 删除自定义属性
      productStandardDelete({ ids: [item.id] }).then(res => {
        if (res.code === 200) {
          this.productStandardInfos[parentIndex].attrs.splice(index, 1)
        }
      })
    },
    getStandardImg(items, index, params) {
      // 设置图片项
      this.$set(
        // 动态设置图片的id
        this.saleStandardInfos[params.parentIndex].attrValueList[index],
        'valueResourceId',
        items[0].id
      )
      this.$set(
        // 动态设置图片的路径
        this.saleStandardInfos[params.parentIndex].attrValueList[index],
        'url',
        items[0].url
      )
    },
    prevStep() {
      this.$store.dispatch('setCache', true)
      this.$store.dispatch('setStep', 'step2')
    },
    save() {
      // 发布商品
      this.$refs.form.validate((valid, item) => {
        if (valid) {
          const productInfos = this.$cms.deepCopy(this.formData)
          productInfos.skuTypeAttrs = this.properties
          productInfos.skus = this.skus
          productInfos.proTypeAttrs = this.attributes
          this.$store.dispatch('setStandardInfo', productInfos).then(param => {
            this.loading = true
            productSave(param).then(res => {
              this.loading = false
              if (res.code === 200) {
                this.$successMessage('新增成功!')
              } else {
                this.$errorMessage('添加失败')
              }
            })
          })
        } else {
          // 定位到错误的那一行
          let name = ''
          for (const key in item) {
            if (item.hasOwnProperty(key)) {
              name = key.replace('.', '_')
              break
            }
          }
          this.errorPosition(name)
        }
      })
    },
    errorPosition(name) {
      // 错误信息定位
      const lay = document.querySelector('#layout')
      const ele = this.$refs[name].$el
      lay.scrollTop = ele.offsetTop - 350
      return false
    },
    restData() {
      // 清除缓存数据
      this.formData = this.$cms.deepCopy(defaultData)
      this.customStandardValue = {} // 自定义规格值
      this.customAttributeValue = {} // 自定义属性值
      this.checkSaleStandardIds = [] // 选中的规格id
      this.checkProductStandardIds = [] // 选中的属性id
      this.properties = [] // 选中的商品规格
      this.attributes = [] // 选中的属性值
      this.saleStandardInfos = [] // 销售属性集合
      this.productStandardInfos = [] // 商品属性集合
      this.skus = [] // 生成的sku信息
      this.attributeGroups = {}
    }
  },
  watch: {
    allCheckedLength: {
      // 监听选中的变化 重新触发sku信息
      handler() {
        this.createdSkus()
      }
    }
  },
  activated() {
    const cacheProductTypeId = this.$store.state.product.productTypeId // 分类id
    const cacheStoreId = this.$store.state.product.storeId // 商家id
    if (
      cacheProductTypeId !== this.productTypeId ||
      cacheStoreId !== this.storeId
    ) {
      // 缓存处理
      this.restData()
      this.getProductInfo()
    }
  },
  mounted() {
    const lay = document.querySelector('#layout')
    lay.addEventListener('scroll', e => {
      let scrollTop = e.target.scrollTop
      const clientHeight = e.target.clientHeight
      const scrollHeigh = e.target.scrollHeight
      if (this.fixed === false) {
        scrollTop += 70
      }
      if (parseInt(scrollTop) > scrollHeigh - clientHeight - 70) {
        this.fixed = false
      } else {
        this.fixed = true
      }
    })
  }
}
</script>
<style lang="scss" scoped>
.w-50 {
  width: 50%;
  display: inline-block;
  margin: 5px 0;
}
.w-100 {
  width: 100%;
  display: block;
}
.attribute-name {
  width: 120px;
  margin-right: 25px;
  font-size: 14px;
  color: #666;
  text-align: right;
}
.icon-zhankai1-copy {
  cursor: pointer;
}
.del {
  cursor: pointer;
  font-size: 14px;
  color: #a3a3a3;
  &:hover {
    color: #fe5f40;
  }
}
.el-icon-delete {
  width: 15px;
  height: 15px;
}
</style>




