  <!--商品信息项-->
  <template>
  <div class="cms-form-layout loading-top product-main-warpper" v-loading="loading">
    <el-form ref="form" :rules="rules" :model="formData" label-width="210px" @submit.native.prevent>
      <h3 class="cms-segmentation">基本信息</h3>
      <el-form-item label="商品分类" prop="product.categoryId">
        {{categoryName}}
      </el-form-item>
      <!-- 只有自营才会有这个选项,商家端不需要这个 -->
      <el-form-item label="选择店铺" prop="product.storeId" ref="product_storeId">
        <el-select v-model="formData.product.storeId" :popper-append-to-body='false' @change="selectStoreCategory">
          <el-option v-for="item in storeItems" :label="item.storeName" :value="item.id" :key="item.id">
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="店铺分类" prop="product.storeCategoryId">
        <el-cascader :options="options" :props="props" :change-on-select='true' v-model="formData.storeCategoryIds" 
        placeholder="请选择分类">
        </el-cascader>
      </el-form-item>
      <el-form-item label="商品名称" prop="product.name" ref="product_name">
        <el-input v-model="formData.product.name"></el-input>
        <span class="gray">商品名称长度控制在100个字符以内</span>
      </el-form-item>
      <!-- 通过类型判断是否为虚拟商品 -->
      <el-form-item label="虚拟规格商品" prop="product.virtualCardTplId" v-if="isVirtual">
        <el-select v-model="formData.product.virtualCardTplId" :popper-append-to-body='false'>
          <el-select v-model="formData.product.templeteId" :popper-append-to-body='false'>
            <el-option label="默认模板" :value="null"></el-option>
          </el-select>
        </el-select>
        <span class="gray">选择虚拟库（卡密）模板即可，点击新建模板增加</span>
      </el-form-item>
      <el-form-item label="货号" prop="product.itemNumber" ref="product_itemNumber">
        <el-input v-model="formData.product.itemNumber"></el-input>
        <span class="gray">货号用于商家管理商品的一种编号，仅供商家使用，最多输入30个字符</span>
      </el-form-item>
      <el-form-item label="品牌" prop="product.brandId">
        <cms-brand v-model="formData.product.brandId">
          <cms-button type='info' size='small'>选择品牌</cms-button>
        </cms-brand>
      </el-form-item>
      <el-form-item label="商品简介" prop="product.summary">
        <el-input type='textarea' :rows='4' v-model="formData.product.summary"></el-input>
        <el-color-picker v-model="formData.product.summaryColor" class="product-color-picker"></el-color-picker>
        <span class="gray">商品简介显示在商品标题下方,长度不超过100字符，右侧的颜色板可设置显示颜色
        </span>
      </el-form-item>
      <el-form-item label="运费模板" prop="product.templeteId">
        <el-select v-model="formData.product.templeteId" :popper-append-to-body='false'>
          <el-option label="默认模板" :value="1"></el-option>
        </el-select>
        <div>
          <table class="sku-table" style='width:380px;margin-top:10px;'>
            <tr>
              <th>运送方式</th>
              <th>运送到</th>
              <th>首件(个)</th>
              <th>运费</th>
              <th>续建(个)</th>
              <th>续费</th>
            </tr>
            <tr>
              <td>快递</td>
              <td>全国</td>
              <td>1</td>
              <td>8</td>
              <td>1</td>
              <td>5</td>
            </tr>
          </table>
        </div>
      </el-form-item>
      <el-form-item label="体积" prop="product.volume" ref='product_volume'>
        <el-input v-model="formData.product.volume">
          <template slot="append">立方米</template>
        </el-input>
      </el-form-item>
      <el-form-item label="重量" prop="product.weight" ref='product_weight'>
        <el-input v-model="formData.product.weight">
          <template slot="append">kg</template>
        </el-input>
      </el-form-item>
      <h3 class="cms-segmentation">商品相册</h3>
      <el-form-item label="短视频" prop="checked">
        <cms-picture-zone type='video' viewType='media' @change="getProductVideo">
          <cms-button type='info' size='small'>选择视频</cms-button>
        </cms-picture-zone>
      </el-form-item>
      <el-form-item label="商品相册" prop="imageResources" ref='imageResources' :error='errorMessage.imageResources' class="imageResource">
        <div class="clearfix">
          <!-- 拖拽组件 -->
          <draggable v-model="formData.imageResources" :options="{animation:150}">
            <cms-picture-zone class="product-img-box" v-for="(item,index) in formData.imageResources" :key="index" :preview='false' :index='index' @change="getProductImg">
              <div class="product-img-warpper">
                <img v-if="formData.imageResources[index]&&formData.imageResources[index].url!==''" :src="$cms.filterUrl(formData.imageResources[index].url)" alt="">
                <span v-else class="img-plus el-icon-plus"></span>
              </div>
              <!-- 图片位置移动 -->
              <div class="img-move-bar" @click.stop>
                <span class="el-icon-arrow-left next" :class="[index===0?'disable':'']" @click.stop="imgMove(index,'next')"></span>
                <span class="el-icon-arrow-right next" :class="[index===4?'disable':'']" @click.stop="imgMove(index,'prev')"></span>
                <span class="el-icon-close delete" @click.stop="imgDelete(index)"></span>
              </div>
            </cms-picture-zone>
          </draggable>
        </div>
        <span class="gray" style="max-width:490px;">
          用于显示在商品详情页介绍图，最多可支持7张，第一张默认为商品主图用于显示商品列表，拖动照片可调整图片顺序
        </span>
      </el-form-item>
      <h3 class="cms-segmentation">详细信息</h3>
      <el-form-item label="">
        <el-radio-group v-model="txtModel" size="small">
          <el-radio-button label="pc">PC端</el-radio-button>
          <el-radio-button label="mobile">移动端</el-radio-button>
        </el-radio-group>
      </el-form-item>

      <el-form-item label="商品详情" prop="checked" v-show="txtModel=='pc'">
        <cms-editor v-model="formData.txt.pcTxt"></cms-editor>
      </el-form-item>
      <el-form-item label="商品详情" prop="checked" v-show="txtModel=='mobile'">
        <cms-editor v-model="formData.txt.mobileTxt"></cms-editor>
      </el-form-item>
      <el-form-item label="顶部版式" prop="roleName">
        <el-select v-model="formData.roleName" :popper-append-to-body='false'>
          <el-option v-for="item in roleList" :label="item" :value="item" :key="item">
          </el-option>
        </el-select>
        <span class="gray">
          选中的版式将插入至PC端商品详情最顶部
        </span>
      </el-form-item>
      <el-form-item label="底部版式" prop="roleName">
        <el-select v-model="formData.roleName" :popper-append-to-body='false'>
          <el-option v-for="item in roleList" :label="item" :value="item" :key="item">
          </el-option>
        </el-select>
        <span class="gray">
          选中的版式将插入至PC端商品详情最底部
        </span>
      </el-form-item>
      <h3 class="cms-segmentation">其他信息</h3>
      <el-form-item label="商品单位" prop="ext.unit">
        <el-input v-model="formData.ext.unit" maxlength="5"></el-input>
        <span class="gray">可输入件、盒、份、个，默认为件
        </span>
      </el-form-item>
      <el-form-item label="商品服务" prop="ext.storeServiceId">
        <el-checkbox-group v-model="formData.ext.storeServiceId">
          <el-checkbox>七天无理由退货</el-checkbox>
        </el-checkbox-group>
      </el-form-item>
      <el-form-item label="是否为销售商品出售" prop="product.sellGoodsSale" ref="product_sellGoodsSale">
        <cms-switch v-model="formData.product.sellGoodsSale"></cms-switch>
      </el-form-item>
      <el-form-item label="是否为提供发票" prop="product.isInvoice" ref="product_isInvoice">
        <cms-switch v-model="formData.product.isInvoice"></cms-switch>
      </el-form-item>
      <el-form-item label="商品关键词" prop="ext.keyword">
        <el-select size="mini" :multiple-limit='5' multiple default-first-option allow-create filterable v-model="formData.ext.keyword" placeholder="请输入商品关键词">
          <el-option v-for="item in keywordItems" :key="item.value" :label="item.label" :value="item.value">
          </el-option>
        </el-select>
        <span class="gray">设置检索关键词,用于商品搜索,最多支持5个</span>
      </el-form-item>
      <el-form-item label="评价标签" prop="ext.evaluation">
        <el-select size="mini" :multiple-limit='10' multiple default-first-option allow-create filterable v-model="formData.ext.evaluation" placeholder="请输入评价标签">
          <el-option v-for="item in evaluationItems" :key="item.value" :label="item.label" :value="item.value">
          </el-option>
        </el-select>
        <span class="gray">买家评价商品时，可对商品选择对应标签进行评价，如性价比高，最多支持10个关键词
        </span>
      </el-form-item>
      <el-form-item label="发布状态" prop="roleName">
        <el-radio-group v-model="formData.product.status">
          <el-radio :label="3">立即发布</el-radio>
          <el-radio :label="8">入库</el-radio>
        </el-radio-group>
        <span class="gray">选择立即发布，商品进入审核、上架阶段，选择入库则商品进入店铺商 品仓库内，仅商家自己可见
        </span>
      </el-form-item>
    </el-form>
    <div class="product-footer" :class="[fixed?'fixed-footer':'']">
      <cms-button style="background-color:#f7f7f7;color:#444444" @click="prevStep">上一步</cms-button>
      <cms-button v-color @click="nextStep"  style="background:#efae00">下一步,设置规格/属性信息</cms-button>
    </div>
  </div>
</template>
  <script>
import axios from 'axios'
import rules from '@/rules'
import { productCategoryGet } from '@/api/product/category.js'
import { selfShopsList } from '@/api/shop/selfShops.js'
import { storeCategoryTree } from '@/api/shop/shopManage.js' // 商家商品分类
import { ckItemNumber } from '@/api/product/product.js'
import draggable from 'vuedraggable'
var defaultData = {
  product: {
    storeId: '',
    storeCategoryId: null,
    categoryId: '',
    name: '',
    virtualCardTplId: null,
    itemNumber: '',
    brandId: '',
    summary: '',
    summaryColor: '#ff0000',
    templeteId: 1,
    weight: '',
    volume: '',
    sellGoodsSale: true,
    isInvoice: true,
    status: 3
  },
  productTypeId: '',
  storeCategoryIds: [],
  imageResources: [
    // 最多五张图片
    {
      remark: '',
      imgResourceId: '',
      url: ''
    },
    {
      remark: '',
      imgResourceId: '',
      url: ''
    },
    {
      remark: '',
      imgResourceId: '',
      url: ''
    },
    {
      remark: '',
      imgResourceId: '',
      url: ''
    },
    {
      remark: '',
      imgResourceId: '',
      url: ''
    }
  ],
  videoResources: [
    {
      remark: '',
      vdResourceId: '',
      url: ''
    }
  ],
  txt: {
    pcTxt: '',
    mobileTxt: '',
    topTplId: null,
    bottomTplId: null
  },
  ext: {
    maxDeductible: 0,
    unit: '件',
    keyword: '',
    evaluation: '',
    storeServiceId: ''
  }
}
export default {
  components: { draggable },
  data() {
    var checkCode = (rule, value, callback) => {
      if (value === '') {
        callback()
      } else {
        ckItemNumber({
          itemNumber: value,
          storeId: this.formData.product.storeId
        }).then(res => {
          if (res.data) {
            callback()
          } else {
            callback(new Error('货号重复'))
          }
        })
      }
    }

    return {
      loading: false,
      status: false,
      fixed: true,
      txtModel: 'pc',
      storeItems: [], // 自营店铺列表
      roleList: ['超级管理员', '运营'],
      options: [],
      props: {
        value: 'id',
        label: 'cateName',
        children: 'childs',
        disabled: 'isAuth'
      },
      keywordItems: [],
      evaluationItems: [],
      formData: Object.assign({}, defaultData),
      categoryName: '',
      isVirtual: false, // 判断是否是虚拟分类
      rules: {
        'product.storeId': [rules.required('必须选择一个店铺', 'change')],
        'product.categoryId': [rules.required('必须选择一个商品分类')],
        'product.itemNumber': [rules.custom(checkCode)],
        'product.name': [
          rules.required('请输入商品名称'),
          rules.max(100, '最多只能输入100个字符')
        ],
        'product.summary': [rules.max(100, '最多只能输入100个字符')],
        'product.volume': [rules.float(), rules.decimal()],
        'product.weight': [rules.float(), rules.decimal()],
        'ext.maxDeductible': [rules.float()],
        imageResources: [rules.required('至少上传一张商品图片')],
        'product.sellGoodsSale': [rules.required()],
        'product.isInvoice': [rules.required()]
      },
      errorMessage: {},
      area: ''
    }
  },
  methods: {
    initData() {
      // 初始化数据
      this.formData.product.categoryId = this.$store.state.product.categoryId // 设置分类id
      this.categoryName = this.$store.state.product.categoryName // 显示分类名称
      this.getRelevantInfos() // 加载相关的接口
      productCategoryGet({ id: this.formData.product.categoryId }).then(res => {
        // 通过商品分类获取到商品类型
        const productTypeId = res.data.productTypeId
        this.formData.productTypeId = productTypeId // 设置商品类型id
        this.isVirtual = res.data.virtual
      })
    },
    selectStoreCategory(value) {
      // 改变店铺
      this.getStroeCategory(value)
    },
    getStroeCategory(id) {
      // 加载对应的商家商品分类
      storeCategoryTree({ storeId: id }).then(res => {
        this.options = []
        const root = [{ id: null, cateName: '默认分类' }]
        this.options = root.concat(res.data)
      })
    },
    getStoreCategoryId(value) {
      this.formData.product.storeCategoryId = value[value.length - 1]
    },
    getRelevantInfos() {
      // 这里获取配套的列表信息
      axios.all([selfShopsList({ page: 1, size: 99999 })]).then(
        axios.spread(storeItems => {
          this.storeItems = storeItems.data.content
        })
      )
    },
    validateImgs() {
      // 验证是否上传了图片
      let state = false
      this.formData.imageResources
      for (const v of this.formData.imageResources) {
        if (v.imgResourceId !== '') {
          state = true
        }
      }
      if (state) {
        return true
      } else {
        this.errorMessage = {}
        setTimeout(() => {
          this.$set(
            this.errorMessage,
            'imageResources',
            '至少上传一张商品图片'
          )
          this.errorPosition('imageResources')
        }, 1)
        return false
      }
    },
    prevStep() {
      // 上一步
      this.$store.dispatch('setCache', true)
      this.$store.dispatch('setStep', 'step1')
    },
    nextStep() {
      // 下一步操作
      this.$refs.form.validate((valid, item) => {
        if (valid) {
          if (this.validateImgs()) {
            // 判断图片是否上传
            const obj = this.$cms.deepCopy(this.formData)
            obj.ext.keyword = obj.ext.keyword.join(',')
            obj.ext.evaluation = obj.ext.evaluation.join(',')
            obj.product.storeCategoryId =
              obj.storeCategoryIds[obj.storeCategoryIds.length - 1]
            this.$store.dispatch('setBaseProductInfo', obj).then(res => {
              this.$store.dispatch('setStep', 'step3')
            })
          }
        } else {
          // 定位到错误的那一行
          let name = ''
          for (const key in item) {
            if (item.hasOwnProperty(key)) {
              name = key.replace('.', '_')
              break
            }
          }
          this.errorPosition(name)
        }
      })
    },
    errorPosition(name) {
      // 错误信息定位
      const lay = document.querySelector('#layout')
      const ele = this.$refs[name].$el
      lay.scrollTop = ele.offsetTop - 350
      return false
    },
    getProductVideo(item, index) {
      this.$set(this.formData.videoResources, index, {
        remark: '',
        vdResourceId: item[0].id,
        url: item[0].url
      })
    },
    getProductImg(item, index) {
      // 获取商品组图
      this.$set(this.formData.imageResources, index, {
        remark: '',
        imgResourceId: item[0].id,
        url: item[0].url
      })
      this.errorMessage = {}
    },
    imgMove(index, status) {
      // 图片移动
      if (status === 'next') {
        if (index === 0) {
          // 第一张不能点
          return false
        } else {
          const tmpItems = this.formData.imageResources[index - 1]
          this.$set(
            this.formData.imageResources,
            index - 1,
            this.formData.imageResources[index]
          )
          this.$set(this.formData.imageResources, index, tmpItems)
        }
      } else {
        if (index === 4) {
          // 最后一张不能点
          return false
        } else {
          const tmpItems = this.formData.imageResources[index + 1]
          this.$set(
            this.formData.imageResources,
            index + 1,
            this.formData.imageResources[index]
          )
          this.$set(this.formData.imageResources, index, tmpItems)
        }
      }
    },
    imgDelete(index) {
      // 删除图片信息
      this.$set(this.formData.imageResources, index, {
        remark: '',
        imgResourceId: '',
        url: ''
      })
    }
  },
  activated() {
    if (!this.$store.state.product.cache) {
      // 不是缓存的就要回复默认数据
      this.formData = this.$cms.deepCopy(defaultData)
    }
    this.initData() // 初始化数据
  },
  mounted() {
    const lay = document.querySelector('#layout')
    lay.addEventListener('scroll', e => {
      let scrollTop = e.target.scrollTop
      const clientHeight = e.target.clientHeight
      const scrollHeigh = e.target.scrollHeight
      if (this.fixed === false) {
        scrollTop += 70
      }
      if (parseInt(scrollTop) > scrollHeigh - clientHeight - 70) {
        this.fixed = false
      } else {
        this.fixed = true
      }
    })
  }
}
</script>
<style lang="scss" scoped>
</style>




