<template>
  <!--商品分类选项-->
  <div class="cms-form-layout ">
    <div class="commonly-class">
      <label class="commonly-class-label">常用分类</label>
      <el-select v-model="checkLocalItem" class="commonly-class-select" value-key='id' @change="getLocalCategoryList">
        <el-option v-for="(item,index) in LocalCategoryList" :key="index" :label="item.name" :value="item.items"></el-option>
      </el-select>
    </div>
    <div class="step-class-warpper">

      <div class="step-class-box" >
         <div class="check-class-title">您当前选择的分类：
        <span class="blue">
          {{categoryName}}
        </span>
      </div>
        <!-- 第一层类目 -->
        <section class="step-class-group">
          <div class="class-group-header">
            请选择分类
          </div>
          <ul class="class-group-body">
            <li class="items" :class="[checkIds[0]&&checkIds[0].id===item.id?'active':'']" v-for="(item,index) in items0" :key="item.id" @click="setCheckItem(item,index,0)">{{item.cateName}}</li>
          </ul>
        </section>
        <!-- 第二层类目 -->
        <section class="step-class-group" v-if="items1.length>0">
          <div class="class-group-header">
            请选择分类
          </div>
          <ul class="class-group-body">
            <li class="items" :class="checkIds[1]&&checkIds[1].id===item.id?'active':''" v-for="(item,index) in items1" :key="item.id" @click="setCheckItem(item,index,1)">{{item.cateName}}</li>
          </ul>
        </section>
        <!-- 第三层类目 -->
        <section class="step-class-group" v-if="items2.length>0">
          <div class="class-group-header">
            请选择分类
          </div>
          <ul class="class-group-body">
            <li class="items" :class="checkIds[2]&&checkIds[2].id===item.id?'active':''" v-for="(item,index) in items2" :key="item.id" @click="setCheckItem(item,index,2)">{{item.cateName}}</li>
          </ul>
        </section>
        <!-- 第四层类目 -->
        <section class="step-class-group" v-if="items3.length>0">
          <div class="class-group-header">
            请选择分类
          </div>
          <ul class="class-group-body">
            <li class="items" :class="checkIds[3]&&checkIds[3].id===item.id?'active':''" v-for="(item,index) in items3" :key="item.id" @click="setCheckItem(item,index,3)">{{item.cateName}}</li>
          </ul>
        </section>
      </div>
    </div>
    <div style="text-align:center;padding:15px 0;">
      <cms-button @click="nextStep" :disabled="categoryId===0">下一步，填写商品信息</cms-button>
    </div>
  </div>
</template>
<script>
import { productCategoryList } from '@/api/product/category.js'
export default {
  data() {
    return {
      checkLocalItem: '', // 常用分类
      items0: [], // 第一分类
      items1: [], // 第二分类
      items2: [], // 第三分类
      items3: [], // 第四分类
      checkIds: [],
      categoryId: 0 // 最后选中的那个分类id
    }
  },
  computed: {
    categoryName() {
      let name = ''
      for (let i = 0; i < this.checkIds.length; i++) {
        if (this.checkIds[i].name !== '' && i === 0) {
          name = this.checkIds[i].name
        } else if (this.checkIds[i].name !== '') {
          name = name + ' > ' + this.checkIds[i].name
        }
      }
      return name
    },
    LocalCategoryList() {
      // 从本地存储获取
      const item = localStorage.getItem('LocalCategoryList')
      let arr = []
      if (item) {
        arr = JSON.parse(item)
      }
      return arr
    }
  },
  methods: {
    getCategoryList(parentId, current) {
      // 获取到分类的列表数据
      if (current === 0) {
        this.items2 = []
        this.items3 = []
        this.$set(this.checkIds, 1, { name: '', id: '' })
        this.$set(this.checkIds, 2, { name: '', id: '' })
        this.$set(this.checkIds, 3, { name: '', id: '' })
      } else if (current === 1) {
        this.items3 = []
        this.$set(this.checkIds, 2, { name: '', id: '' })
        this.$set(this.checkIds, 3, { name: '', id: '' })
      } else if (current === 2) {
        this.$set(this.checkIds, 3, { name: '', id: '' })
      } else if (current === 3) {
        return true
      } else {
        this.checkIds = [
          { name: '', id: '' },
          { name: '', id: '' },
          { name: '', id: '' },
          { name: '', id: '' }
        ]
      }
      productCategoryList({ parentId: parentId }).then(res => {
        if (current === 0) {
          this.items1 = res.data
        } else if (current === 1) {
          this.items2 = res.data
        } else if (current === 2) {
          this.items3 = res.data
        } else if (current === 3) {
          return false
        } else {
          this.items0 = res.data
        }
      })
    },
    nextStep() {
      // 选好分类后下一步
      this.setLocalCategoryList() // 设置常用分类项
      this.$store.dispatch('setCateGoryInfo', {
        categoryId: this.categoryId,
        parentInfo: this.checkIds
      })
    },
    getLocalCategoryList(item) {
      item.forEach((v, i) => {
        if (v.id !== '') {
          this.getCategoryList(v.id, i)
          this.categoryId = v.id
        }
      })
      this.checkIds = this.$cms.deepCopy(item)
    },
    setLocalCategoryList() {
      // 设置常用分类项
      if (localStorage.getItem('LocalCategoryList')) {
        // 存在就追加进去
        const items = JSON.parse(localStorage.getItem('LocalCategoryList'))
        let flag = true
        const obj = {
          id: items.length + 1,
          name: this.categoryName,
          items: this.checkIds
        }
        for (const v of items) {
          if (v.name === obj.name) {
            flag = false
            break
          }
        }
        if (flag) {
          items.push(obj)
          const jsonStr = JSON.stringify(items)
          localStorage.setItem('LocalCategoryList', jsonStr)
        }
      } else {
        const obj = {
          id: 1,
          name: this.categoryName,
          items: this.checkIds
        }
        const jsonStr = JSON.stringify([obj])
        localStorage.setItem('LocalCategoryList', jsonStr)
      }
    },
    setCheckItem(item, index, current) {
      // 点击显示下层分类
      this.checkLocalItem = ''
      this.categoryId = item.id
      this.$set(this.checkIds, current, { name: item.cateName, id: item.id })
      this.getCategoryList(item.id, current)
    }
  },
  activated() {
    const status = this.$store.state.product.cache
    if (!status) {
      this.getCategoryList()
    }
  }
}
</script>

<style>
</style>



