<template>
  <div >
    <cms-breadcrumb to="/productMange/list" type='list' class="pos-breadcrumb" @click='clearInfo'>
      <li>{{$route.name}}</li>
    </cms-breadcrumb>
    <div class="main-layout">
      <div class="fixed-bar">
        <cms-prompt class="pos-prompt ">
          <li>带
            <span style="color:#ff0000"> * </span> 为必填项 </li>
        </cms-prompt>
        <ul class="cms-steps">
          <li class="cms-step" :class="currentView=='step1'?'active':''"  >
            <span class="cms-step-index">1</span>
            <span class="cms-step-name">
              <i class="iconfont icon-fenlei-sel"></i>
              选择商品分类
            </span>
          </li>
          <li class="cms-step" :class="currentView=='step2'?'active':''" >
            <span class="cms-step-index">2</span>
            <span class="cms-step-name">
              <i class="iconfont icon-zongjie"></i>
              填写商品信息
            </span>
          </li>
          <li class="cms-step" :class="currentView=='step3'?'active':''" >
            <span class="cms-step-index">3</span>
            <span class="cms-step-name">
              <i class="iconfont icon-gaojishuxing"></i>
              设置规格/属性信息
            </span>
          </li>
          <li class="cms-step" :class="currentView=='step4'?'active':''" >
            <span class="cms-step-index">4</span>
            <span class="cms-step-name">
              <i class="iconfont icon-guanlian"></i>
              关联设置
            </span>
          </li>
        </ul>
      </div>
      <div class="product-layout" id="productLayout">
         <keep-alive>       
             <component :is="currentView"></component>
         </keep-alive>
      </div>
    </div>
  </div>
</template>

<script>
import step1 from './step1'
import step2 from './step2'
import step3 from './step3'
import step4 from './step4'
export default {
  components: {
    step1: step1,
    step2: step2,
    step3: step3,
    step4: step4
  },
  data() {
    return {

    }
  },
  computed: {
    currentView() {
      return this.$store.state.product.step
    }
  },
  methods: {
    setStep(step) {
      this.$store.dispatch('setStep', step)
    },
    clearInfo() {
      this.$store.dispatch('clearProductInfo')
    }
  }
  // activated() {
  //   if (this.$route.query.type === 'save') {
  //     this.$store.dispatch('setStep', 'step1')
  //   }
  // }
}
</script>

<style <style lang="scss" scoped>
</style>





